import React, { useState, useEffect } from "react";
import { Grid, Typography, Divider } from "@mui/material";
import { getAllTestimonial } from "../../apiCalls";

const Testimonials = () => {
  const [testimonials] = useState([
    {
      name: "Sherin",
      image: "https://i.pinimg.com/236x/3a/84/43/3a84438c147f5cc10f1453a3b1b05090.jpg",
      message:
        "We've seen tremendous growth in our customer engagement since adopting AppBowl. The support team is always quick to resolve any queries, making us feel valued as customers. Not only has our interaction with clients improved, but we've also been able to implement innovative marketing strategies that have significantly increased our reach and visibility in the market.",
    },
    {
      name: "Smith",
      image: "https://i.pinimg.com/236x/51/a0/e6/51a0e6fd63938cea4eb84333c5f32327.jpg",
      message:
        "AppBowl's platform transformed how we manage projects. The intuitive design and seamless integration have drastically boosted our team's productivity. We've been able to streamline our processes, which has led to faster project completions and happier clients. Additionally, the ability to customize the features to fit our specific needs has made a world of difference in our day-to-day operations.",
    },
    {
      name: "Angela",
      image: "https://i.pinimg.com/564x/65/2a/ec/652aec00e482bcee4d6714b15e9946d8.jpg",
      message:
        "When I research companies online, I don’t just want to hear the company’s pitch. I want to hear from its customers. Customer testimonials play a crucial role in my decision-making process. I appreciate how AppBowl encourages its users to share their experiences, providing genuine insights into how the platform has impacted their businesses. It’s this transparency that builds trust and credibility, making me feel more confident in choosing their services.",
    },
  ]);


  
  const [data, setData] = useState([]); 
  const [error, setError] = useState(''); 

  const fetchTestimonialsWithHandling = async () => {
    try {
      const data = await getAllTestimonial();
      if(data.success){
        const shuffledArray = [...data?.data].sort(() => 0.5 - Math.random());

        const selectedItems = shuffledArray.slice(0, 3);

        setData(selectedItems);
      }
    } catch (err) {
      setError('Failed to load testimonials. Please try again later.');
      console.error('Error fetching testimonials:', err);
    }
  };

  useEffect(() => {
    fetchTestimonialsWithHandling();
  }, []);


  return (
    <Grid
      sx={{
        backgroundImage:
          "url(https://img.freepik.com/premium-vector/modern-simple-minimal-elegant-white-background-with-shiny-lines_249611-5952.jpg)",
        backgroundSize: "cover",
        backgroundPosition: "center",
        backgroundRepeat: "no-repeat",
        width: "100%",
      }}
    >
      <Grid
        container
        justifyContent="center"
        alignItems="center"
        sx={{
          width: { xs: "98%", sm: "98%", md: "80%" },
          margin: "0px auto",
          padding: "30px 0",
        }}
      >
        <Typography variant="h6" fontWeight="bold" gutterBottom>
          Testimonials
        </Typography>
        {data?.length &&
          data.map((item, index) => (
            <React.Fragment key={index}>
              <Grid
                container
                alignItems="flex-start"
                spacing={2}
                sx={{
                  padding: "16px",
                  flexDirection: { xs: "column", sm: "row" },
                  textAlign: { xs: "left", sm: "inherit" },
                }}
              >
                {/* Image */}
                <Grid item xs={3} sm={2}>
                  <img
                    src={
                      item.image ||
                      "https://images.pexels.com/photos/415829/pexels-photo-415829.jpeg?auto=compress&cs=tinysrgb&dpr=1&w=500"
                    }
                    alt="person"
                    style={{
                      width: "100px",
                      height: "100px",
                      borderRadius: "50%",
                    }}
                  />
                </Grid>
                {/* Message */}
                <Grid item xs={12} sm={10}>
                  <Typography variant="subtitle2">
                    {item.message ||
                      "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua."}
                  </Typography>
                </Grid>
              </Grid>
              <Divider />
            </React.Fragment>
          ))}
      </Grid>
    </Grid>
  );
};

export default Testimonials;
